import React,{createContext,useReducer,useEffect} from 'react'
import './cart.css'
import ContextCart from './ContextCart';
import { products } from './products';
import { reducer } from './reducer';

// Declaring createContext - for passing product values
// using useContext 
export const CartContext=createContext();

const initialState={
    item:products,
    totalAmount:0,
    totalItem:0
};

const Cart = () => {

    // using usereducer 
    const [state, dispatch] = useReducer(reducer, initialState)

    // For remove single element 
    const removeItem = (id) =>{
        return dispatch({
            type : "REMOVE_ITEM",
            payload:id
        })
    }

    // Clear the cart 
    const clearCart = () =>{
        return dispatch({
            type:"CLEAR_CART"
        })
    }

    // Increment the cart 
    const increment = (id) =>{
        return dispatch({
            type:"INCREMENT",
            payload:id
        })
    }

    // Decrement the cart 
    const decrement = (id) =>{
        return dispatch({
            type:"DECREMENT",
            payload:id
        })
    }

    // We will use useEffect to update the data 
    useEffect(() => {
       dispatch({
          type:"GET_TOTAL"
        })
        console.log("Updated")
    },[state.item])

    return (
        <>
           <CartContext.Provider value={{...state, removeItem, clearCart,increment, decrement}}>
            <ContextCart></ContextCart>
           </CartContext.Provider> 
        </>
    )
}

export default Cart
