export const reducer = (state, action) => {

    // Remove single item 
    if (action.type === "REMOVE_ITEM") {
        return {
            ...state,
            item: state.item.filter((curElm) => {
                return curElm.id !== action.payload;
            }),
        };
    }

    // Clear all item 
    if (action.type === "CLEAR_CART") {
        return {
            ...state,
            item: []
        };
    }

    // Incrementing 
    if (action.type === "INCREMENT") {
        let upDatedData = state.item.map((curElm) => {
            if (curElm.id === action.payload) {
                return {
                    ...curElm, quantity: curElm.quantity + 1
                };
            }
            return curElm;
        });
        return { ...state, item: upDatedData }
    }

    // dECREMENTING 
    if (action.type === "DECREMENT") {
        let upDatedData = state.item.map((curElm) => {
            if (curElm.id === action.payload) {
                return {
                    ...curElm, quantity: curElm.quantity - 1
                };
            }
            return curElm;
        }).filter((curElm) => {
            return curElm.quantity !== 0;
        });
        return {
            ...state, item: upDatedData
        }
    }

    // total amount and total item 
    if (action.type==="GET_TOTAL") {
        let {totalItem,totalAmount}=state.item.reduce((accum,curVal)=>{
            let {quantity,price}=curVal;
            let updatedTotalAmount=price*quantity;
            accum.totalAmount+=updatedTotalAmount
            accum.totalItem+=quantity
            return accum
        },{
            totalItem:0,totalAmount:0
        })
        return{...state,totalItem,totalAmount}
    }


    return state
}